import Link from 'next/link'
import React, { useState, useEffect } from 'react'
import UserDetailsModal from './UserDetailsModal'

const MainScreenWrapper = ({
    children,
    screenHeader = 'Header',
}) => {
    const [showModal, setShowModal] = useState(false);
    const [userDetails, setUserDetails] = useState(null);

    useEffect(() => {
        // Check if user details exist in localStorage
        const savedDetails = localStorage.getItem('userDetails');
        
        if (savedDetails) {
            const parsedDetails = JSON.parse(savedDetails);
            // Check if details are from today (optional - you can modify this logic)
            const savedDate = new Date(parsedDetails.timestamp);
            const today = new Date();
            const isToday = savedDate.toDateString() === today.toDateString();
            
            if (isToday) {
                setUserDetails(parsedDetails);
            } else {
                // Clear old data and show modal
                localStorage.removeItem('userDetails');
                setShowModal(true);
            }
        } else {
            // No saved details, show modal
            setShowModal(true);
        }
    }, []);

    const handleModalSubmit = (details) => {
        setUserDetails(details);
        setShowModal(false);
    };

    return (
        <>
            <UserDetailsModal 
                isOpen={showModal} 
                onSubmit={handleModalSubmit} 
            />
            
            <div className={`justify-start relative w-full flex h-full items-start flex-col min-h-screen gap-1 text-black ${showModal ? 'blur-sm pointer-events-none' : ''}`} >
                {/* {headerItem} */}
                <div className="w-full h-full pt-1">
                    {children}
                </div>
            </div>
        </>
    )
}
export default MainScreenWrapper
